.Header {
  display: flex;
  justify-content: center;
  background-color: #ece8e8;
  border-bottom: 4px solid rgb(31, 65, 41);
}
.Header-Content {
  display: flex;
  justify-content: space-between;
  width: 1100px;
  padding-left: 1rem;
  padding-right: 1rem;
}

.Header img {
  width: 3.3rem;
  padding-top: 0.5rem;
  padding-bottom: 0.2rem;
}


.project {
  padding-top: 1rem;
}

.project a {
  font-size: 20px;
  font-weight: 700;
  text-decoration: none;
  color: rgb(7, 7, 7);
  display: flex;
}
.project a div {
  padding-left: 0.4rem;
}

.link-div {
  padding-right: 1rem;
  padding-top: .2rem;
}

.contact a {
  padding: 0.7rem;
  border: 2px solid rgb(0, 0, 0);
  border-radius: 0.5rem;
  margin: 0.2rem;
}

/* mobil navigation */

.mobil-navigation{
  display: none;
  z-index: 200;
  word-break: break-all;
}

.mobil-menu {
  position: absolute;
  right: 0;
  padding-right: .5rem;
  background-color: white;
  padding: .5rem;
  border-top: 4px solid rgb(31, 65, 41);
  border-bottom: 4px solid rgb(31, 65, 41);
  left: 0;
  background-color: white;
  animation: showMobileNavbar ease-out .3s;
}
.mobil-menu a, .hmbr-contact {
  color: black;
  text-decoration: none;
  font-weight: 600;
  font-size: 1.1rem;
}
.hmbr-item {
  margin: .5rem 0rem .5rem 0rem;
  border: 2px solid black;
  padding: .5rem;
  text-align: center;
}

.mobil-navigation-button {
  background-image: url("./İmg/humburgerLogo.svg");
  display: block;
  width:2.6rem;
  height: 2.6rem;
  background-repeat: no-repeat;
  background-size: contain;
  border: none;
  margin-top: .8rem;
  margin-bottom: 1rem;
}

@keyframes showMobileNavbar {
  0% {
    transform: scaleY(0);
  }
  100%{
    transform: scaleY(1);
  }
}


@media (max-width: 440px) {
  .contact a {
    word-break: break-all;
  }
}

@media (max-width: 500px) {
  .project {
    display: none !important;
  }
  .mobil-navigation {
    display: block;
  }
}

